import './App.css';
import React, { useState, useEffect } from 'react';
import {
  initViewer,
  loadModel
} from './utils';
import Box from '@mui/material/Box';
import Tooltip from '@mui/material/Tooltip';
import TextField from '@mui/material/TextField';


function App() {
  const Autodesk = window.Autodesk;
  const [viewer, setviewer] = useState('')
  const [allID, setallID] = useState([])
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);
  const [Results, setResults] = useState([]);

  /* initialize the viewer */
  useEffect(() => {
    (async function () {
      try {
        const response = await initViewer(document.getElementById("forgeviewer"));
        loadModel(
          response, "dXJuOmFkc2sub2JqZWN0czpvcy5vYmplY3Q6YXBzLWNvZGVwZW4tYmFja2VuZC8wMV9yYWNfYmFzaWNfc2FtcGxlX3Byb2plY3QucnZ0"
        );
        setviewer(response);

        /* get instanceTree when GEOMETRY_LOADED_EVENT completed */
        const getinstanceTree = () => {
          const instanceTree = response.model.getData().instanceTree;
          const rootId = instanceTree.getRootId();
          if (rootId) {
            var queue = [];
            var alldbId = new Set();
            queue.push(rootId);
            while (queue.length > 0) {
              var node = queue.shift();
              alldbId.add(node);
              instanceTree.enumNodeChildren(node, function (childrenIds) {
                queue.push(childrenIds);

              })
            };
          }
          setallID(alldbId);
          setIsButtonDisabled(false)
        }

        response.addEventListener(Autodesk.Viewing.GEOMETRY_LOADED_EVENT, getinstanceTree);
        return () => {
          response.removeEventListener(Autodesk.Viewing.GEOMETRY_LOADED_EVENT, getinstanceTree);
        }

      } catch (e) {
        console.error(e);
      }
    })();
  }, []);

  /* fetch the search query */
  function handleSearch(userinput) {
    try {
      let active = true;
      async function fetchdata() {
        var searchoutput = await viewer.model.getPropertyDb().executeUserFunction(function userFunction(pdb, r) {
          var result = []
          var allID = r[0]
          var searchterm = String(r[1]).toLowerCase()
          if (searchterm) {
            pdb.enumObjects(dbId => {
              var p = pdb.getObjectProperties(dbId);
              for (var i = 0; i < p.properties.length; i++) {
                var disVal = String(p.properties[i].displayValue)
                var idx = disVal.toLowerCase().indexOf(searchterm)
                if ((idx !== -1) && (allID.has(p.dbId))) {
                  var mid = disVal.substring(idx, idx + searchterm.length)

                  /* Truncate long suffix/prefix terms */
                  var pre = disVal.substring(0, idx)
                  if (pre.length > 20) {
                    pre = '...' + pre.slice(-20)
                  }
                  var suf = disVal.substring(idx + searchterm.length, disVal.length)
                  if (suf.length > 20) {
                    suf = suf.slice(0, 20) + '...'
                  }

                  /* Only return one matching attribute/value pairs */
                  result.push([dbId, p.name, String(p.properties[i].displayName), pre, mid, suf, disVal]);
                  break
                }
              }
            }
            );
          }
          return result;
        }, [allID, userinput])

        /* Only update with the last async result */
        if (active) {
          setResults(await searchoutput)
        }

      };

      fetchdata()

      return () => {
        active = false;
      };

    } catch (e) {
      console.error(e);
    }
  }

  /* function for the click event */
  function handleClick(e, id) {
    if (!e.target.classList.contains('MuiTooltip-tooltip')) {
      try {
        viewer.isolate(id);
        viewer.select(id);
        viewer.fitToView(id);
      } catch (e) {
        console.error(e);
      }
    }
  }

  return (
    <div className="splitScreen">
      <div className="leftPane" id="forgeviewer"></div>
      <div className="rightPane">
        <div className="bar">
          <TextField autoComplete="off" className="input" disabled={isButtonDisabled} type="text" onChange={e => handleSearch(e.target.value)} label="Search metadata" variant="standard" />
        </div>
        <div className="result">
          {
            Results.map(function (data) {
              return <Box component="section" key={data[0]} onClick={(e) => handleClick(e, data[0])} sx={{ p: 2, border: '1px dashed grey' }}>
                <div className="boxName"><span style={{ textDecoration: "underline" }}>{data[1]}</span></div>
                <div className="boxAttribute"><span style={{ color: "#808080" }}>Attribute:</span> {data[2]}</div>
                <Tooltip title={data[6]} placement="bottom-start"
                  slotProps={{
                    popper: {
                      modifiers: [
                        {
                          name: 'offset',
                          options: {
                            offset: [35, -14],
                          },
                        },
                      ],
                    },
                  }}
                >
                  <div className="boxValue"><span style={{ color: "#808080" }}>Value:</span> {data[3]}<span style={{ color: "#ff0000", fontWeight: "bold" }}>{data[4]}</span>{data[5]}</div>
                </Tooltip>
              </Box>;
            }
            )
          }
        </div>
      </div>
    </div>
  );
}

export default App;
